export function formatDate(dateString) {
  const date = new Date(dateString);
  let dd = date.getDate() + 1;
  let mm = date.getMonth() + 1;

  const yyyy = date.getFullYear();
  if (dd < 10) {
    dd = '0' + dd;
  }
  if (mm < 10) {
    mm = '0' + mm;
  }
  return `${mm}/${dd}/${yyyy}`;
}

export function formatTimeAgo(timeValue, includeMinutes = false) {
  const weeks = Math.floor(timeValue / (60 * 24 * 7));
  const days = Math.floor((timeValue - weeks * 60 * 24 * 7) / (60 * 24));
  const hours = Math.floor((timeValue - weeks * 60 * 24 * 7 - days * 24 * 60) / 60);
  const minutes = Math.floor(timeValue - weeks * 60 * 24 * 7 - (timeValue - days * 24 * 60) - hours * 60);
  let ret = '';

  if (weeks > 0) {
    ret = weeks + 'weeks';
  }

  if (days > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    ret += days + 'days';
  }

  if (hours > 0) {
    if (ret !== '') {
      ret += ' ';
    }
    ret += hours + 'hours';
  }

  if (minutes > 0 && includeMinutes) {
    if (ret !== '') {
      ret += ' ';
    }
    ret += minutes + 'minutes';
  }

  return ret + ' ago';
}
